import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import "./form3.css";
import userdp from "../../../images/user-dp.jpg"
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles({
    root: {
        display: "flex",
        flexDirection: "column",
        flexWrap: "wrap",
    },
    dvTabs: {
        display: "flex",
        flexDdirection: "row",
        float: "left",
        justifyContent: "space-between",
        maxWidth: "500px",
        margin: "1rem",
        marginBottom: "-2rem"
    },
    tab1: {
        backgroundColor: "#fff",
        border: "3px solid rgb(203, 209, 203)",
        maxWidth: "70px",
        height: "70px",
        borderRadius: "100px",
        fontWeight: "bold",
        fontSize: "30px",
        color: "rgb(0, 0, 0)",
        cursor: "pointer",
    },
    tab2: {
        backgroundColor: "#fff",
        border: "3px solid rgb(203, 209, 203)",
        maxWidth: "70px",
        height: "70px",
        borderRadius: "100px",
        fontWeight: "bold",
        fontSize: "30px",
        color: "rgb(0, 0, 0)",
        cursor: "pointer",
    },
    tab3: {
        backgroundColor: "rgb(255, 123, 0)",
        border: "none",
        maxWidth: "70px",
        height: "70px",
        borderRadius: "100px",
        fontWeight: "bold",
        color: "rgb(0, 0, 0)",
        cursor: "pointer",
    },
    tabsStrings: {
        textAlign: "center",
    },
    lineTabs: {
        width: "10px",
        height: "10px",
        backgroundColor: "rgb(235, 243, 235)",
        marginTop: "30px",
    }
});

export const ProfileForm3 = () => {
    const classes = useStyles();
    const [profilePic, setProfilePic] = useState({
        systemUrl: "",
        createdURL: ""
    })
    const [imgUrl, setImgUrl] = useState(userdp)
    const [displayPicture, setDisplayPicture] = useState();
    const [summary, setSummary] = useState()
    const profilePicHandleChange = (event) => {
        setProfilePic({
            systemUrl: event.target.files[0].path,
            createdURL: URL.createObjectURL(event.target.files[0])
        });
        setImgUrl(profilePic.createdURL)
        console.log(profilePic)
    }
    const summaryHandleChange = (event) => {
        setSummary(event.target.value);
    }
    const Form3_Submit = (e) => {
        e.preventDefault();
        console.log(profilePic, summary)
        // window.location.replace("/MainProfile")
    }
    useEffect(() => {
        setDisplayPicture(
            <img src={imgUrl} alt="" id="userProfileImg" width="100" height="100" />
        )
    }, [profilePic])
    return (
        <>
            {/* <!-- Form 3 --> */}

            <div className={classes.root}>
                <div className={`${classes.dvTabs} row col`}>
                    <button className={`${classes.tab1} dvOne-tab`} id="dvOne-tab">
                        <h3 className={`${classes.tabsStrings} tabs-strings`} id="tabs-strings">1</h3>
                    </button>
                    <div className={`${classes.lineTabs} line-tabs col`}></div>

                    <button className={`${classes.tab2} dvTwo-tab`} id="dvTwo-tab">
                        <h3 className={`${classes.tabsStrings} tabs-strings`} id="tabs-strings">2</h3>
                    </button>
                    <div className={`${classes.lineTabs} line-tabs col`}></div>

                    <button className={`${classes.tab3} dvThree-tab`} id="dvThree-tab" >
                        <h3 className={`${classes.tabsStrings} tabs-strings`} id="tabs-strings">3</h3>
                    </button>
                </div>




                {/* <Link to="/ProfileForm1">back to home</Link> */}
                {/* <h2>Form 3</h2> */}
                <form class="form-group col-md-6 profileForm-form3" onSubmit={Form3_Submit}>
                    <div class="mb-3">
                        {displayPicture}
                    </div>

                    <div class="mb-3">
                        <label for="formFileSm" class="form-label">Select your own photograph size picture</label>
                        <input
                            class="form-control form-control-sm"
                            id="formFileSm"
                            type="file"
                            accept=".jpg, .png/*"
                            onChange={profilePicHandleChange}
                        />
                    </div>
                    <div class="mb-3">
                        <label for="textareaSummary" class="form-label">
                            Summary </label>
                        <textarea
                            class="form-control"
                            id="textareaSummary"
                            placeholder="Summary ..."
                            value={summary}
                            onChange={summaryHandleChange}
                        ></textarea>
                    </div>
                    <div class="form-group col-md-6">
                        <input type="submit" class="btn-signup btn-lg" value="Submit" />
                    </div>
                </form>
            </div>
        </>
    )
}